import React, { Component } from 'react';
import { ReactiveBase, CategorySearch, ReactiveList,DataSearch} from '@appbaseio/reactivesearch';
import './App.css';
import planecrop from './img/planecrop.png';
import Demo from "./Demo.jsx";

class App extends Component {
    constructor(props) {
        super(props);

        this.getInnerRef = this.getInnerRef.bind(this);
        this.getLocation = this.getLocation.bind(this);
    }

    innerRef;
    getInnerRef(ref) {
        this.innerRef = ref;
    }

    getLocation() {
        this.innerRef && this.innerRef.getLocation();
    }
	render() {
		const { getInnerRef, getLocation } = this;
		return (
		    <div className="container" style={{"padding-left":"10%", "padding-right":"10%"}}>
		        {/* 
		        <article style={{ textAlign: "center" }}>
		                <Demo onError={error => console.log(error)} ref={getInnerRef} />
		                <button
		                    className="pure-button pure-button-primary"
		                    onClick={getLocation}
		                    type="button"
		                >
		                    Get location
		                </button>
	            </article>
	            */}
		        <div className="row" style={{"margin-bottom":"-30px"}}>
		          <div className="col align-self-center">
		            <h1>Get Spirited Away</h1>
		          </div>
		        </div>
		        <div className="row" style={{"padding-bottom":"20px"}}>
		          <div className="col align-self-center" >
		           <img src={planecrop} alt="plane"></img>
		          </div>
	        	</div>
				<ReactiveBase
					app="getspiritedaway_ver4"
					credentials="SYAF5SJDT:e6c658e3-559f-4eec-ac8f-ef64c98b41dc">
					<DataSearch
					  className="btrow"
					  style={{"padding-left":"10%", "padding-right":"10%","marginBottom":"20"}}
					  autosuggest={true}
					  componentId="search"
					  dataField={[
					    'description',
					    'description.autosuggest',
					    'description.keyword'
					  ]}
					  fieldWeights={[
					    1,
					    1,
					    1
					  ]}
					  fuzziness={0}
					  highlightField={[
					    'description'
					  ]}
					  placeholder="you put in your ideal vacation. we will find you the cheapest spirit flight"
					/>
					<div className="col">
					<ReactiveList
						componentId="result"
						dataField="price"
						sortby="asc"
						pagination={true}
						size={10}
						react={{
					    	and: [
					        'search'
					    	]
						}}
						onData={this.vacaresults}
					
					/>	
					</div>
				</ReactiveBase>
			</div>
		);
	}


	vacaresults(res){
		var price=res.price.toFixed(0);
		var snip=res.description.substring(0,120)+"..."
		return(
				<div className="flex book-content"> 
				<img src={res.url} alt="GSA" className="url-image"/>
				<div className="book-header" style={{contentRight:100}}>{res.dest}
				</div>
				<div className="justify-center" style={{marginLeft:20}}>Lonely Planet
				<div className="flex">{snip}</div>
				</div>
				<div style={{marginLeft:20}}>{res.departmonth}{res.date}</div>
				<div className="col price-header" style={{marginRight:20}}>${price}</div>
				</div>
			);
	}

}
export default App;